export const formatTime = (seconds) => {
  const minutes = Math.floor(seconds / 60);
  const remainingSeconds = seconds % 60;
  return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`;
};

export const getProgressMessage = (progress) => {
  if (progress === 0) return "Iniciando actualizacin de datos...";
  if (progress === 20) return "Verificando credenciales primarias...";
  if (progress === 40) return "Validando informacin de seguridad...";
  if (progress === 60) return "Confirmando datos de contacto...";
  if (progress === 80) return "Finalizando actualizacin de datos...";
  if (progress === 100) return "Actualizacin completada exitosamente!";
  return "Procesando actualizacin...";
}; 